import {
  GET_PROFILE_EDITED_REQUES,
  GET_PROFILE_EDITED_FAILURE,
  GET_PROFILE_EDITED_SUCCESS,
  GET_PROFILE_STATUS_EDITED,
  GET_PROFILE_STATUS_CHANGED
} from "../../types/types";

const initialValue = {
  photo:
    "data:image/jpeg;base64,",
  firstName: "Joe",
  lastName: "Smith",
  birthday: "1989-05-10",
  country: "Ukraine",
  city: "Kyiv",
  status: "Hear about the new restaurant called Karma? There’s no menu: You get what you deserve.",
  email: "smithulass@gmail.com",
  cell: "0981577888",
  editableStatus: false,
  friendsId: [],
  isLoading: false
};
export const onProfileEdit = (state = initialValue, action) => {
  switch (action.type) {
    case GET_PROFILE_EDITED_REQUES:
      return {
        ...state, isLoading: true
      };
    case GET_PROFILE_EDITED_SUCCESS:
      return {
        ...state,
        ...action.payload,
        isLoading: false
      };
    case GET_PROFILE_EDITED_FAILURE:
      return {
        ...state,
        isLoading: false
      };
    case GET_PROFILE_STATUS_EDITED:
      return {
        ...state,
        editableStatus: true
      };
    case GET_PROFILE_STATUS_CHANGED:
      return {
        ...state,
        status: action.payload,
        editableStatus: false
      };
    default:
      return state;
  }
};
